<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import { transitionTime } from '../helpers/transitionTime'
  import { cards } from '../stores/cards'
  import { params, navigate } from '../components/Router.svelte'
  import ScrollView from '../components/ScrollView.svelte'
  import Header from '../components/Header.svelte'
  import Button from '../components/Button.svelte'

  $: card = $cards.find((c) => c.id === parseInt($params.get('id')))
  $: cardIsExist = Boolean(card)

  // If card is not exist
  onMount(() => {
    if (!cardIsExist) {
      cards.add('md')
      navigate('/editor?id=' + $cards[0].id, true)
    }
  })

  function focus(node, timeout) {
    setTimeout(() => node.focus(), timeout)
  }

  function autoResize(node) {
    function resize() {
      node.style.height = 'auto'
      node.style.height = node.scrollHeight + 'px'
    }
    resize()
    node.oninput = () => resize()
  }
</script>

<style>
  textarea {
    overflow: hidden;
    resize: none;

    width: 100%;
    height: auto;

    padding: 1.5rem 0;

    color: inherit;
    background: var(--background);
    border: none;

    font-size: 1rem;
    font-family: inherit;
    line-height: var(--line-height);
  }
</style>

<ScrollView>
  <Header>
    <Button
      icon="navigate_before"
      class="transparent"
      on:click={() => window.history.back()} />
    <Button
      icon="delete"
      class="transparent"
      on:click={() => {
        cards.remove(card.id)
        window.history.back()
      }} />
  </Header>

  <article>
    <div class="container">
      {#if cardIsExist}
        <textarea
          name="Editor"
          placeholder="Type here..."
          out:fade={{ duration: transitionTime / 2 }}
          use:focus={transitionTime}
          use:autoResize
          on:input={(card.changed = Date.now())}
          bind:value={$cards[$cards.indexOf(card)].content} />
      {/if}
    </div>
  </article>
</ScrollView>
